/* Reset and base styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b9b9b9;
  color: #111;
}

main {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
}

/* Page styles */
.loading-container,
.error-container {
  text-align: center;
  font-size: 1.2rem;
  padding: 2rem;
}

.error-container {
  color: #d90000;
}

/* Auth Form */
.auth-container {
  border: 1px solid #eee;
  padding: 1.5rem 2rem;
  border-radius: 8px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-width: 350px;
  margin-top: 1rem;
}

.auth-form input {
  padding: 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.auth-form .btn-primary {
  padding: 0.75rem;
  background: #0070f3;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 1rem;
}

.auth-form .btn-secondary {
  padding: 0.75rem;
  background: #555;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 1rem;
}

.auth-error {
  color: #d90000;
  margin-top: 0.5rem;
}

/* Question Form */
.question-form h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.question-form p {
  font-size: 1.1rem;
  color: #444;
  margin-top: 0;
}

.question-block {
  margin-bottom: 1.5rem;
  border: 1px solid #eee;
  padding: 1rem 1.5rem;
  border-radius: 8px;
}

.question-block .question-text {
  font-weight: bold;
  font-size: 1.1rem;
}

.button-group {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.button-group .btn-answer {
  flex-grow: 1;
  padding: 0.75rem;
  font-size: 1rem;
  background: #fff;
  color: #333;
  border: 1px solid #ccc;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.button-group .btn-answer.selected {
  background: #0070f3;
  color: white;
  border-color: #0070f3;
}

.btn-submit {
  width: 100%;
  padding: 1rem;
  font-size: 1.1rem;
  background: #000;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 1rem;
}

.btn-submit:hover {
  background: #333;
}

.profile-form {
  border: 1px solid #eee;
  padding: 1.5rem 2rem 2rem;
  border-radius: 8px;
}

.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.25rem;
}

.input-group label {
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.input-group input {
  padding: 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.success-message {
  padding: 1rem;
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
  border-radius: 4px;
  margin-bottom: 1rem;
}

/* ... all your existing styles ... */

/* Group Page */
.group-header {
  text-align: center;
  border-bottom: 1px solid #eee;
  padding-bottom: 1.5rem;
  margin-bottom: 2rem;
}

.group-header h1 {
  margin-bottom: 0.5rem;
}

.group-header code {
  font-size: 1.5rem;
  background: #eee;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
}

.group-header .header-links {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 1rem;
}

.header-links a {
  color: #0070f3;
  text-decoration: none;
}
.header-links a:hover {
  text-decoration: underline;
}

.member-list {
  display: grid;
  gap: 1.5rem;
}

.member-card {
  position: relative;
  border: 1px solid #ddd;
  padding: 1.5rem;
  border-radius: 8px;
}

.member-card h3 {
  margin: 0 0 1rem 0;
}

.you-tag {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: #eee;
  padding: 0.25rem 0.5rem;
  font-size: 0.9rem;
  border-radius: 4px;
}

.social-links {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.social-links a {
  padding: 0.5rem 1rem;
  border: 1px solid #0070f3;
  color: #0070f3;
  text-decoration: none;
  border-radius: 4px;
  font-weight: 500;
}
.social-links a:hover {
  background: #0070f3;
  color: white;
}

.no-socials {
  color: #777;
  font-style: italic;
  margin: 1rem 0 0 0;
}

.btn-sign-out {
  background: none;
  border: none;
  color: #d90000;
  cursor: pointer;
  font-size: 1rem;
  padding: 0;
}
.btn-sign-out:hover {
  text-decoration: underline;
}

.btn-sign-out-profile {
  background: #d90000;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  margin: 2rem auto 0;
}
.btn-sign-out-profile:hover {
  background: #b00000;
}
.validation-error {
  padding: 1rem;
  background-color: #f8d7da; /* Red */
  color: #721c24;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  margin-bottom: 1rem;
  text-align: center;
}
.header-actions {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.btn-sign-in-header {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  background: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
}
.btn-sign-in-header:hover {
  background: #f9f9f9;
}


/* Login Modal */
.login-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.login-modal-content {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  position: relative;
}

.login-modal-content .auth-form {
  max-width: none; /* Override auth form max-width */
}

.btn-close-modal {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #888;
}

.page-nav-header {
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.page-nav-header a {
  color: #0070f3;
  text-decoration: none;
  font-weight: 500;
}
.page-nav-header a:hover {
  text-decoration: underline;
}

/* Answers Page */
.answers-container {
  border: 1px solid #eee;
  padding: 1.5rem 2rem;
  border-radius: 8px;
}

.answers-list {
  margin-top: 2rem;
}

.answer-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid #f0f0f0;
}
.answer-item:last-child {
  border-bottom: none;
}

.answer-item .question-text {
  margin: 0;
  padding-right: 1rem;
}

.answer-tag {
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-weight: bold;
  font-size: 1rem;
  flex-shrink: 0;
}

.answer-tag.yes {
  background-color: #d1f0d1;
  color: #006400;
}

.answer-tag.no {
  background-color: #f8d7da;
  color: #721c24;
}

.pfp-uploader {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.pfp-uploader label {
  position: relative;
  cursor: pointer;
  display: inline-block;
}

.pfp-preview {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid #eee;
  object-fit: cover;
  display: block;
}

.pfp-edit-text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  text-align: center;
  padding: 0.5rem 0;
  font-size: 0.9rem;
  border-bottom-left-radius: 75px; /* Hacky, but works for circle */
  border-bottom-right-radius: 75px;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.pfp-uploader label:hover .pfp-edit-text {
  opacity: 1;
}

/* Hide the ugly file input */
#file-input {
  display: none;
}

.upload-progress-bar {
  width: 100%;
  height: 10px;
  background: #f0f0f0;
  border-radius: 5px;
  margin-bottom: 1.5rem;
  overflow: hidden;
}

.upload-progress-bar div {
  height: 100%;
  background: #0070f3;
  transition: width 0.3s ease;
}

/* Group Page Member Card Avatar */
.member-card {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.member-avatar {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #eee;
}

.member-info {
  flex-grow: 1;
}

.upload-progress-bar {
  width: 100%;
  background-color: #f3f3f3;
  border-radius: 5px;
  margin-top: 10px;
}

.upload-progress-bar-inner {
  height: 10px;
  width: var(--progress-width, 0%);
  background-color: #4caf50;
  border-radius: 5px;
  transition: width 0.3s ease-in-out;
}